
* {
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  height: 100%;
}
#map-box {
  width: 100vw;
  height: 100vh;
  z-index: 1;
}
.items-on-map {
  position: absolute;
  z-index: 10;
}
#main-box {
  width: 10rem;
  height: 2.1875rem;
  line-height: 2.1875rem;
  bottom: 1.75rem;
  left: 50%;
  margin-left: -5rem;
  font-size: 0.9375rem;
  text-align: center;
  border: 2px solid limegreen;
  border-radius: 1.25rem;
}
#tools-box {
  bottom: 1.4375rem;
  right: 0.9375rem;
}
#tools-box img {
  width: 2.8125rem;
  height: 2.8125rem;
}
#settings-box {
  bottom: 1.4375rem;
  left: 0.9375rem;
}
#settings-box img {
  width: 2.5rem;
  height: 2.5rem;
}
#settings-page {
  width: 80%;
  height: 100%;
  padding-left: 0.625rem;
  position: absolute;
  z-index: 100;
  top: 0;
  font-size: 1.0625rem;
  background-color: #efefef;
  transition: left .2s linear;
  border-radius: 0.625rem;
}
.settings-page-button {
  width: 90%;
  height: 3.125rem;
  border-top: 1px solid limegreen;
  border-bottom: 1px solid limegreen;
  margin: 1.25rem auto;
  line-height: 3.125rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.settings-page-button img:first-child {
  padding: 0 0.625rem;
  width: 1.5625rem;
  height: 1.5625rem;
}
.settings-page-button .content-message {
  width: 80%;
}
.settings-page-button .goto-pic {
  width: 0.5rem;
  height: 0.875rem;
  float: right;
  padding: 0 0.625rem;
}
.profile-photo-box {
  width: 6.25rem;
  height: 6.25rem;
  margin: 3.125rem auto;
  border-radius: 3.125rem;
  overflow: hidden;
}
#curtain {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;
  background-color: #000;
  opacity: 0.5;
}
#curtain.fade-enter-active, #curtain.fade-leave-active {
  transition: opacity .5s;
}
#curtain.fade-enter, #curtain.fade-leave-active {
  opacity: 0;
}



